import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	components: {
		uniIcon
	},
	name: 'Bugbtn',
	data () {
		return {
			num: this.value || 0
		};
	},
	props: {
		value: {
			default: 0
		},
		max: {
			default: 50
		}
	},
	watch: {
		value (val) {
			this.num = val;
		}
	},
	methods: {
		clkMinus () {
			this.num > 0 && (this.num -= 1);
			this.$emit('input', this.num);
			this.$emit('click', {'num': this.num});
		},
		clkPlus () {
			if (this.num < this.max) {
				this.num += 1;
				this.$emit('input', this.num);
				this.$emit('click', {'num': this.num});
			} else {
				this.$emit('click', {'error': '已达上限'});
			}
		}
	}
}
